@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  height: 100vh;
  background: linear-gradient(45deg, #ff6200, #fd9346);
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 400px;
  background-color: #fff;
  border-radius: 25px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

h1 {
  font-size: 24px;
  font-weight: 600;
}

.textarea {
  width: 100%;
  height: 150px;
  resize: none;
  outline: none;
  padding: 5px;
  font-weight: 500;
}

.speakerIcon {
  margin: -15px 0;
  font-size: 50px;
}

.textToSpeechBtn {
  width: 50%;
  height: 40px;
  opacity: .8;
  cursor: pointer;
  background: linear-gradient(45deg, #ff6200, #fd9346);
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 500;
  transition: all .1s ease-in-out;
}

.textToSpeechBtn:hover {
  opacity: 1;
}

.textToSpeechBtn:active {
  transform: scale(.95);
}

